import { Component, OnInit } from '@angular/core';
import { DatacollectionService } from '../Datacollection-service.service';

@Component({
  selector: 'app-summary',
  templateUrl: './summary.component.html',
  styleUrls: ['./summary.component.sass']
})
export class SummaryComponent implements OnInit {

  constructor(private DashboardService: DatacollectionService) { }
  summaryCharts: any;
  breadscrums = [
    {
      title: 'Summary',
      items: ['Explainability'],
      active: 'Summary'
    }
  ];
  ngOnInit() {
    if (false) this.getData();
    else{
      alert("Please reach out to contact@sigmared.ai")
      const targetUrl = `${window.location.origin}/#/exp/EDS`;
      window.location.href = targetUrl;
    }
  }

  private getData() {
    this.DashboardService.getSummary("null")
      .subscribe(data => {
        console.log(data, ":datatdat =>")
        if (data) {
          this.summaryCharts = data;
        }
      },(err:any)=>{alert("EDA Data not found!")})
  }
}
