import { Component, OnInit } from '@angular/core';
import { DatacollectionService } from '../Datacollection-service.service'
import { EChartsOption } from 'echarts';

@Component({
  selector: 'app-Intersection',
  templateUrl: './Intersection.component.html',
  styleUrls: ['./Intersection.component.scss']
})

export class IntersectionComponent implements OnInit {
  overallEvalDropDown: [] = [];
  overallEvalData: [] = [];
  public BiasData: EChartsOption = null;
  public table: any = [];


  graph_line_chart: EChartsOption = {
    tooltip: {
      trigger: 'axis'
    },

    toolbox: {
      show: !1
    },
    xAxis: [
      {
        type: 'category',
        data: ['2000', '2001', '2002', '2003', '2004', '2005'],
        axisLabel: {
          fontSize: 10,
          color: '#9aa0ac'
        }
      }
    ],
    yAxis: [
      {
        type: 'value',
        axisLabel: {
          fontSize: 10,
          color: '#9aa0ac'
        }
      }
    ],
    series: [
      {
        name: 'sales',
        type: 'bar',
        data: [0.668],
        markLine: {
          data: [
            {
              type: 'average'
            }
          ]
        }
      }

    ],
    color: ['#9f78ff', '#32cafe']
  };


  breadscrums = [
    {
      title: 'Dashboard',
      items: ['Explainability'],
      active: 'Explainability'
    }
  ];
  getExplainData: any = null
  constructor(private DatacollectionService: DatacollectionService) { }

  ngOnInit() {

    // if(false) this.getData()
    // else{
    //   alert("Please reach out to contact@sigmared.ai")
    //   const targetUrl = `${window.location.origin}/#/exp/EDS`;
    //   window.location.href = targetUrl;
    // }
    this.getData()
  }

  getData() {
    this.DatacollectionService.getExplanability("null")
      .subscribe(data => {
        console.log(data, ":datatdat =>")
        if (data) {
          this.getExplainData = data;
        }
      })
  }


}
