import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainComponent } from './main/main.component';
import { mainDashboardRes} from './mainDashboardRes/mainDashboardRes.component';

import { Dashboard2Component } from './dashboard2/dashboard2.component';
import { Dashboard3Component } from './dashboard3/dashboard3.component';
import { IntersectionComponent } from './Intersection/Intersection.component';
import { EDA} from './EDA/EDA.component';
import { SummaryComponent } from './summary/summary.component';


const routes: Routes = [
  {
    path: '',
    redirectTo: 'mainRes',
    pathMatch: 'full'
  },

  {
    path: 'EDS',
    component: EDA
  },

  {
    path: 'performance',
    component: mainDashboardRes
  },

  // {
  //   path: 'dataDrift',
  //   component: MainComponent
  // },
  {
    path: 'explainability',
    component: IntersectionComponent
  },
  {
    path: 'whatif',
    component: Dashboard2Component
  },
  {
    path: 'summary',
    component: SummaryComponent
  },
  // {
  //   path: 'dashboard3',
  //   component: Dashboard3Component
  // }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ExplainabilitysRoutingModule {}
