import { Component, OnInit } from '@angular/core';
import { EChartsOption } from 'echarts';
import { DatacollectionService } from '../Datacollection-service.service'

import {
  ApexAxisChartSeries,
  ApexChart,
  ApexTooltip,
  ApexPlotOptions,
  ApexDataLabels,
  ApexYAxis,
  ApexXAxis,
  ApexNonAxisChartSeries,
  ApexLegend,
  ApexResponsive,
  ChartComponent,
  ApexFill
} from 'ng-apexcharts';

export type smallBarChart = {
  series: ApexAxisChartSeries;
  chart: ApexChart;
  plotOptions: ApexPlotOptions;
  tooltip: ApexTooltip;
};

export type smallAreaChart = {
  series: ApexAxisChartSeries;
  chart: ApexChart;
  yaxis: ApexYAxis;
  xaxis: ApexXAxis;
};
export type smallColumn = {
  series: ApexAxisChartSeries;
  chart: ApexChart;
  dataLabels: ApexDataLabels;
  plotOptions: ApexPlotOptions;
  responsive: ApexResponsive[];
  xaxis: ApexXAxis;
  legend: ApexLegend;
  fill: ApexFill;
};

export type smallLineChart = {
  series: ApexAxisChartSeries;
  chart: ApexChart;
  yaxis: ApexYAxis;
  xaxis: ApexXAxis;
};

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss']
})
export class MainComponent implements OnInit {
  graph_line_chart: EChartsOption = {
    tooltip: {
      trigger: 'axis'
    },
    // legend: {
    //   data: ['sales', 'purchases'],
    //   textStyle: {
    //     color: '#9aa0ac'
    //   }
    // },
    toolbox: {
      show: !1
    },
    xAxis: [
      {
        type: 'category',
        data: ['2000', '2001', '2002', '2003', '2004', '2005'],
        axisLabel: {
          fontSize: 10,
          color: '#9aa0ac'
        }
      }
    ],
    yAxis: [
      {
        type: 'value',
        axisLabel: {
          fontSize: 10,
          color: '#9aa0ac'
        }
      }
    ],
    series: [
      {
        name: 'sales',
        type: 'bar',
        data: [0.668],
        // markPoint: {
        //   data: [
        //     {
        //       type: 'max',
        //       name: '???'
        //     },
        //     {
        //       type: 'min',
        //       name: '???'
        //     }
        //   ]
        // },
        markLine: {
          data: [
            {
              type: 'average'
            }
          ]
        }
      }

      // {
      //   name: 'purchases',
      //   type: 'bar',
      //   data: [35, 45, 47, 10, 35, 70],
      //   markLine: {
      //     data: [
      //       {
      //         type: 'average'
      //       }
      //     ]
      //   }
      // }
    ],
    color: ['#9f78ff', '#32cafe']
  };

  public smallBarChart: any;
  public smallAreaChart: any;
  public smallColumnChart: any;
  public smallLineChart: any;
  public BiasData: any = null;
  objectKeys = Object.keys;
  public areaChartOptions: any;
  public areaChartData: any;
  public areaChartLabels: any;
  public areaChartPlugins: any;
  dataList: any
  dataList2: any = null;
  option1: any = "null";
  option2: any = "null";
  option3: any = "null";
  option4: any = "null";

  threshold1: any;
  threshold2: any;
  public radarChartLabels: any;
  public radarChartData: any;
  public radarChartType: any;
  public radarChartColors: any;
  public radarChartOptions: any;
  public radarChartPlugins: any;
  public sampleData = [
    31, 40, 28, 44, 60, 55, 68, 51, 42, 85, 77, 31, 40, 28, 44, 60, 55
  ];

  breadscrums = [
    {
      title: 'Data Drift',
      items: ['Mlops'],
      active: 'Data Drift'
    }
  ];
  dataDriftResult: EChartsOption = null;
  dataDriftResult2: EChartsOption = null;

  dataDriftTable: any;
  constructor(private DatacollectionService: DatacollectionService) { }

  ngOnInit() {
    this.getData()
    this.getData2()
    this.getData3()

    // this.cardChart1();
    // this.cardChart2();
    // this.cardChart3();
    // this.cardChart4();
    // this.chart1();
    // this.chart2();
  }
  private getData() {
    this.DatacollectionService.dropDown1()
      .subscribe((data) => {
        if (data) {
          console.log(data, "asa")
          if (data?.data_list) {
            this.dataList = data?.data_list
          }
        }
      })
  }

  private getData2() {
    this.DatacollectionService.thrshold1()
      .subscribe((data) => {
        if (data) {
          console.log(data, "asa1")
          // if(data?.data_list){
          this.threshold1 = data
          // }
        }
      })
  }

  private getData3() {
    this.DatacollectionService.thrshold2()
      .subscribe((data) => {
        if (data) {
          console.log(data, "asa2")
          // if(data?.data_list){
          this.threshold2 = data
          // }
        }
      })
  };

  submitDrift() {
    console.log(this.option1, this.option2, this.option3, this.option4)
    this.DatacollectionService.getDataDriftResult(this.option1,this.option2,this.option3,this.option4)
    .subscribe((data) => {
      if (data) {
        if (data?.ref_hist) {
          console.log(data?.ref_hist, "data?.ref_hist")
          this.dataDriftResult = data?.ref_hist;
          this.dataDriftResult2 = data?.inc_hist;
          this.dataDriftTable = data?.test_table
        }
      }
    })

  }

  public onChange2(data: any) {
    // console.log(data,"option")
    this.option1 = data
    this.DatacollectionService.dropDown2Feature(data)
      .subscribe((data) => {
        if (data) {
          if (data?.feat_list) {
            console.log(data?.feat_list, "data?.feat_list")

            this.dataList2 = data?.feat_list
          }
        }
      })
  }

  private cardChart1() {
    this.smallBarChart = {
      chart: {
        type: 'bar',
        width: 200,
        height: 50,
        sparkline: {
          enabled: true
        }
      },
      plotOptions: {
        bar: {
          columnWidth: '40%'
        }
      },
      series: [
        {
          name: 'income',
          data: this.sampleData
        }
      ],
      tooltip: {
        fixed: {
          enabled: false
        },
        x: {
          show: false
        },
        y: {},
        marker: {
          show: false
        }
      }
    };
  }

  addGraph(impact: string, range: string, series: any, xAxis: []) {
    // console.log(impact,series,xAxis)
    this.graph_line_chart['xAxis'][0]['data'] = xAxis;
    series[0]['type'] = "bar"
    this.graph_line_chart['series'] = series;
    // console.log(this.graph_line_chart,"graph")
    return this.graph_line_chart;
  }

  private cardChart2() {
    this.smallAreaChart = {
      series: [
        {
          name: 'order',
          data: this.sampleData
        }
      ],
      chart: {
        type: 'area',
        height: 50,
        sparkline: {
          enabled: true
        }
      },
      stroke: {
        curve: 'straight'
      },
      colors: ['#00E396'],
      xaxis: {
        labels: {
          show: false
        }
      },
      legend: {
        show: false
      },
      yaxis: {
        show: false
      },
      grid: {
        show: false
      }
    };
  }
  private cardChart3() {
    this.smallColumnChart = {
      chart: {
        type: 'bar',
        width: 200,
        height: 50,
        sparkline: {
          enabled: true
        }
      },
      plotOptions: {
        bar: {
          columnWidth: '40%'
        }
      },
      series: [
        {
          name: 'income',
          data: this.sampleData
        }
      ],

      tooltip: {
        fixed: {
          enabled: false
        },
        x: {
          show: false
        },
        y: {},
        marker: {
          show: false
        }
      }
    };
  }
  private cardChart4() {
    this.smallLineChart = {
      series: [
        {
          name: 'Users',
          data: this.sampleData
        }
      ],
      chart: {
        type: 'line',
        height: 50,
        sparkline: {
          enabled: true
        }
      },
      stroke: {
        curve: 'straight',
        colors: ['#FEB019'],
        width: 4
      },
      tooltip: {
        fixed: {
          enabled: false
        },
        x: {
          show: false
        },
        marker: {
          show: false
        }
      },
      xaxis: {
        labels: {
          show: false
        }
      },
      legend: {
        show: false
      },
      yaxis: {
        show: false
      },
      grid: {
        show: false
      }
    };
  }

  private chart1() {
    this.areaChartOptions = {
      elements: {
        point: {
          radius: 0,
          hitRadius: 5,
          hoverRadius: 5
        }
      },
      legend: {
        display: true,
        labels: {
          fontColor: '#9aa0ac'
        },
        position: 'top'
      },
      scales: {
        xAxes: [
          {
            display: false
          }
        ],
        yAxes: [
          {
            display: false,
            ticks: {
              beginAtZero: true
            }
          }
        ]
      }
    };

    this.areaChartPlugins = [
      {
        afterLayout: (chart) => {
          const ctx = chart.chart.ctx;
          const gradientStroke = ctx.createLinearGradient(0, 0, 0, 150);
          const dataset = chart.data.datasets[0];

          gradientStroke.addColorStop(0, '#5555FF');
          gradientStroke.addColorStop(1, '#9787FF');
          dataset.backgroundColor = gradientStroke;
          dataset.borderColor = 'transparent';
          dataset.pointBorderColor = 'transparent';
          dataset.pointBackgroundColor = 'transparent';
          dataset.lineTension = '0.4';

          const gradientStroke2 = ctx.createLinearGradient(0, 0, 0, 150);
          const dataset2 = chart.data.datasets[1];
          gradientStroke2.addColorStop(0, '#FF55B8');
          gradientStroke2.addColorStop(1, '#FF8787');
          dataset2.backgroundColor = gradientStroke2;
          dataset2.borderColor = 'transparent';
          dataset2.pointBorderColor = 'transparent';
          dataset2.pointBackgroundColor = 'transparent';
          dataset2.lineTension = '0.4';
        }
      }
    ];

    this.areaChartData = [
      {
        label: 'Foods',
        data: [24, 18, 16, 18, 24, 36, 28]
      },
      {
        label: 'Electronics',
        data: [20, 22, 30, 22, 18, 22, 30]
      }
    ];

    this.areaChartLabels = ['MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT', 'SUN'];

    // area chart end
  }

  private chart2() {
    this.radarChartPlugins = [
      {
        afterLayout: (chart) => {
          const ctx = chart.chart.ctx;
          const gradientStroke = ctx.createLinearGradient(0, 0, 0, 150);
          const dataset = chart.data.datasets[0];

          gradientStroke.addColorStop(0, 'rgba(85, 85, 255, 0.9)');
          gradientStroke.addColorStop(1, 'rgba(151, 135, 255, 0.8)');
          dataset.backgroundColor = gradientStroke;
          dataset.borderColor = 'transparent';
          dataset.pointBackgroundColor = 'transparent';
          dataset.pointBorderColor = 'transparent';
          dataset.pointHoverBackgroundColor = 'transparent';
          dataset.pointHoverBorderColor = 'transparent';
          dataset.pointHitRadius = 50;

          const gradientStroke2 = ctx.createLinearGradient(0, 0, 0, 150);
          const dataset2 = chart.data.datasets[1];
          gradientStroke2.addColorStop(0, 'rgba(255, 85, 184, 0.9)');
          gradientStroke2.addColorStop(1, 'rgba(255, 135, 135, 0.8)');
          dataset2.backgroundColor = gradientStroke2;
          dataset2.borderColor = 'transparent';
          dataset2.pointBackgroundColor = 'transparent';
          dataset2.pointBorderColor = 'transparent';
          dataset2.pointHoverBackgroundColor = 'transparent';
          dataset2.pointHoverBorderColor = 'transparent';
          dataset2.pointHitRadius = 50;
        },
        beforeDatasetsDraw: (chart) => {
          chart.ctx.shadowColor = 'rgba(0, 0, 0, 0.25)';
          chart.ctx.shadowBlur = 40;
        },
        afterDatasetsDraw: (chart) => {
          chart.ctx.shadowColor = 'rgba(0, 0, 0, 0)';
          chart.ctx.shadowBlur = 0;
        }
      }
    ];

    this.radarChartLabels = ['MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT', 'SUN'];

    this.radarChartData = [
      { data: [25, 59, 90, 81, 60, 82, 52], label: 'Product', fill: true },
      { data: [40, 100, 40, 90, 40, 90, 84], label: 'Services', fill: true }
    ];
    this.radarChartType = 'radar';
    this.radarChartColors = [
      {
        backgroundColor: ['rgba(109, 144, 232,0.8)']
      },
      {
        backgroundColor: ['rgba(255, 140, 96,0.8)']
      }
    ];
    this.radarChartOptions = {
      legend: {
        display: true,
        labels: {
          fontColor: '#9aa0ac'
        },
        position: 'top'
      },
      gridLines: {
        display: false
      },
      scale: {
        ticks: {
          maxTicksLimit: 1,
          display: false
        }
      }
    };
    // end radar chart
  }
}
