import { Component, OnInit } from '@angular/core';
import { UntypedFormControl } from '@angular/forms';
import { EChartsOption } from 'echarts';
import { DatacollectionService } from '../Datacollection-service.service'



@Component({
  selector: 'app-EDA',
  templateUrl: './EDA.component.html',
  styleUrls: ['./EDA.component.scss']
})
export class EDA implements OnInit {


  tmp: any =  {
    data: [
        { x: [1, 2, 3], y: [2, 6, 3], type: 'scatter', mode: 'lines+points', marker: {color: 'red'} },
        { x: [1, 2, 3], y: [2, 5, 3], type: 'bar' },
    ],
    layout: {width: 320, height: 240, title: 'A Fancy Plot'}
};
  chartArrayOptions: any;
  public smallBarChart: any;
  public smallAreaChart: any;
  public smallColumnChart: any;
  public smallLineChart: any;
  public setDataMetricImpact: any = false;

  public areaChartOptions: any;
  public areaChartData: any;
  public areaChartLabels: any;
  public areaChartPlugins: any;

  public radarChartLabels: any;
  public radarChartData: any;
  public radarChartType: any;
  public radarChartColors: any;
  public radarChartOptions: any;
  public radarChartPlugins: any;
  public sampleData = [
    31, 40, 28, 44, 60, 55, 68, 51, 42, 85, 77, 31, 40, 28, 44, 60, 55
  ];
  liveData: any = null
  barchart1: EChartsOption = {

    "legend": {},
    "tooltip": {},
    "dataset": {
      "source": [
        [
          "Data Source",
          "Reference",
          "Incoming"
        ],
        [
          "workclass",
          5.72,
          5.92
        ],
        [
          "native-country",
          1.75,
          1.68
        ],
        [
          "occupation",
          5.73,
          5.94
        ]
      ]
    },
    "xAxis": {
      "type": "category"
    },
    "yAxis": {},
    "series": [
      {
        "type": "bar"
      },
      {
        "type": "bar"
      }
    ]
  }
  pie_chart: EChartsOption = {
    tooltip: {
      trigger: 'item',
      formatter: '{a} <br/>{b} : {c} ({d}%)'
    },
    legend: {
      data: ['Incoming', 'Reference', 'Data 3', 'Data 4', 'Data 5'],
      textStyle: {
        color: '#9aa0ac',
        padding: [0, 5, 0, 5]
      }
    },

    series: [
      {
        name: 'Chart Data',
        type: 'pie',
        radius: '55%',
        center: ['50%', '48%'],
        data: [
          {
            "value": 10257,
            "name": "Incoming"
          },
          {
            "value": 23932,
            "name": "Reference"
          }
        ]
      }
    ],
    color: ['#575B7A', '#DE725C', '#DFC126', '#72BE81', '#50A5D8']
  };

  breadscrums = [
    {
      title: 'Dashboard',
      items: ['Explainability'],
      active: 'EDA'
    }
  ];
  constructor(private DashboardService: DatacollectionService) { }
  selected = new UntypedFormControl(0);
  dataList: []
  EDAdata: any = []
  loading: Boolean = true

  tabs = ['Live', 'Historic'];
  addTab(selectAfterAdding: boolean) {
    this.tabs.push('New');

    if (selectAfterAdding) {
      this.selected.setValue(this.tabs.length - 1);
    }
  }
  _ngform: any = {}
  removeTab(index: number) {
    this.tabs.splice(index, 1);
  }
  ngOnInit() {
    this.loading = true
    this.cardChart1();
    this.cardChart2();
    this.cardChart3();
    this.cardChart4();
    this.chart1();
    this.chart2();
    this.getData()
  }

  getData() {
    this.DashboardService.getEda("null")
      .subscribe(data => {
        console.log(data, ":datatdat =>")
        if (data) {
          this.createPlotly(data);
        }
      }, (err: any) => { alert("EDA Data not found!") }, () => this.loading = false)
  }


  private createPlotly(data: any){
    this.chartArrayOptions = [...data]
    console.log(this.chartArrayOptions)
  }

  private createCharts(data: any) {
    this.chartArrayOptions = data.map(chart => {
      console.log(chart)
      let chartType = (chart.data[0].type === 'box' || chart.data[0].type === 'violin')  ? 'boxplot' : chart.data[0].type === 'histogram' ? 'bar' : chart.data[0].type || 'line'; // Default chart type
      const seriesData = chart.data.map(s => {
        return {
          type: chartType,
          data: s.x && s.y ? s.x.map((x, index) => [x, s.y[index]]) : s.y || s.x
        }
      })
      const xAxisType = seriesData[0].data[0].length === 2 ? typeof seriesData[0].data[0][0] === 'number' ? 'value' : 'category' : 'value';
      //const xAxisType = typeof chart.data[0]?.x[0] === 'number' ? 'value' : 'category';
      return {
        title: {
          text: chart.name,
          subtext: chart.text,
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985'
            }
          }
        },
        xAxis: {
          type: xAxisType // Changed from 'category' to 'value'
          // Optionally, you can specify additional properties like min, max, interval, etc.
        },
        yAxis: {
          type: 'value'
        },
        series: seriesData
      }
    })
    console.log(this.chartArrayOptions)
  }


  private cardChart1() {
    this.smallBarChart = {
      chart: {
        type: 'bar',
        width: 200,
        height: 50,
        sparkline: {
          enabled: true
        }
      },
      plotOptions: {
        bar: {
          columnWidth: '40%'
        }
      },
      series: [
        {
          name: 'income',
          data: this.sampleData
        }
      ],
      tooltip: {
        fixed: {
          enabled: false
        },
        x: {
          show: false
        },
        y: {},
        marker: {
          show: false
        }
      }
    };
  }
  private cardChart2() {
    this.smallAreaChart = {
      series: [
        {
          name: 'order',
          data: this.sampleData
        }
      ],
      chart: {
        type: 'area',
        height: 50,
        sparkline: {
          enabled: true
        }
      },
      stroke: {
        curve: 'straight'
      },
      colors: ['#00E396'],
      xaxis: {
        labels: {
          show: false
        }
      },
      legend: {
        show: false
      },
      yaxis: {
        show: false
      },
      grid: {
        show: false
      }
    };
  }
  private cardChart3() {
    this.smallColumnChart = {
      chart: {
        type: 'bar',
        width: 200,
        height: 50,
        sparkline: {
          enabled: true
        }
      },
      plotOptions: {
        bar: {
          columnWidth: '40%'
        }
      },
      series: [
        {
          name: 'income',
          data: this.sampleData
        }
      ],

      tooltip: {
        fixed: {
          enabled: false
        },
        x: {
          show: false
        },
        y: {},
        marker: {
          show: false
        }
      }
    };
  }
  private cardChart4() {
    this.smallLineChart = {
      series: [
        {
          name: 'Users',
          data: this.sampleData
        }
      ],
      chart: {
        type: 'line',
        height: 50,
        sparkline: {
          enabled: true
        }
      },
      stroke: {
        curve: 'straight',
        colors: ['#FEB019'],
        width: 4
      },
      tooltip: {
        fixed: {
          enabled: false
        },
        x: {
          show: false
        },
        marker: {
          show: false
        }
      },
      xaxis: {
        labels: {
          show: false
        }
      },
      legend: {
        show: false
      },
      yaxis: {
        show: false
      },
      grid: {
        show: false
      }
    };
  }

  private chart1() {
    this.areaChartOptions = {
      elements: {
        point: {
          radius: 0,
          hitRadius: 5,
          hoverRadius: 5
        }
      },
      legend: {
        display: true,
        labels: {
          fontColor: '#9aa0ac'
        },
        position: 'top'
      },
      scales: {
        xAxes: [
          {
            display: false
          }
        ],
        yAxes: [
          {
            display: false,
            ticks: {
              beginAtZero: true
            }
          }
        ]
      }
    };

    this.areaChartPlugins = [
      {
        afterLayout: (chart) => {
          const ctx = chart.chart.ctx;
          const gradientStroke = ctx.createLinearGradient(0, 0, 0, 150);
          const dataset = chart.data.datasets[0];

          gradientStroke.addColorStop(0, '#5555FF');
          gradientStroke.addColorStop(1, '#9787FF');
          dataset.backgroundColor = gradientStroke;
          dataset.borderColor = 'transparent';
          dataset.pointBorderColor = 'transparent';
          dataset.pointBackgroundColor = 'transparent';
          dataset.lineTension = '0.4';

          const gradientStroke2 = ctx.createLinearGradient(0, 0, 0, 150);
          const dataset2 = chart.data.datasets[1];
          gradientStroke2.addColorStop(0, '#FF55B8');
          gradientStroke2.addColorStop(1, '#FF8787');
          dataset2.backgroundColor = gradientStroke2;
          dataset2.borderColor = 'transparent';
          dataset2.pointBorderColor = 'transparent';
          dataset2.pointBackgroundColor = 'transparent';
          dataset2.lineTension = '0.4';
        }
      }
    ];

    this.areaChartData = [
      {
        label: 'Foods',
        data: [24, 18, 16, 18, 24, 36, 28]
      },
      {
        label: 'Electronics',
        data: [20, 22, 30, 22, 18, 22, 30]
      }
    ];

    this.areaChartLabels = ['MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT', 'SUN'];

    // area chart end
  }

  private chart2() {
    this.radarChartPlugins = [
      {
        afterLayout: (chart) => {
          const ctx = chart.chart.ctx;
          const gradientStroke = ctx.createLinearGradient(0, 0, 0, 150);
          const dataset = chart.data.datasets[0];

          gradientStroke.addColorStop(0, 'rgba(85, 85, 255, 0.9)');
          gradientStroke.addColorStop(1, 'rgba(151, 135, 255, 0.8)');
          dataset.backgroundColor = gradientStroke;
          dataset.borderColor = 'transparent';
          dataset.pointBackgroundColor = 'transparent';
          dataset.pointBorderColor = 'transparent';
          dataset.pointHoverBackgroundColor = 'transparent';
          dataset.pointHoverBorderColor = 'transparent';
          dataset.pointHitRadius = 50;

          const gradientStroke2 = ctx.createLinearGradient(0, 0, 0, 150);
          const dataset2 = chart.data.datasets[1];
          gradientStroke2.addColorStop(0, 'rgba(255, 85, 184, 0.9)');
          gradientStroke2.addColorStop(1, 'rgba(255, 135, 135, 0.8)');
          dataset2.backgroundColor = gradientStroke2;
          dataset2.borderColor = 'transparent';
          dataset2.pointBackgroundColor = 'transparent';
          dataset2.pointBorderColor = 'transparent';
          dataset2.pointHoverBackgroundColor = 'transparent';
          dataset2.pointHoverBorderColor = 'transparent';
          dataset2.pointHitRadius = 50;
        },
        beforeDatasetsDraw: (chart) => {
          chart.ctx.shadowColor = 'rgba(0, 0, 0, 0.25)';
          chart.ctx.shadowBlur = 40;
        },
        afterDatasetsDraw: (chart) => {
          chart.ctx.shadowColor = 'rgba(0, 0, 0, 0)';
          chart.ctx.shadowBlur = 0;
        }
      }
    ];

    this.radarChartLabels = ['MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT', 'SUN'];

    this.radarChartData = [
      { data: [25, 59, 90, 81, 60, 82, 52], label: 'Product', fill: true },
      { data: [40, 100, 40, 90, 40, 90, 84], label: 'Services', fill: true }
    ];
    this.radarChartType = 'radar';
    this.radarChartColors = [
      {
        backgroundColor: ['rgba(109, 144, 232,0.8)']
      },
      {
        backgroundColor: ['rgba(255, 140, 96,0.8)']
      }
    ];
    this.radarChartOptions = {
      legend: {
        display: true,
        labels: {
          fontColor: '#9aa0ac'
        },
        position: 'top'
      },
      gridLines: {
        display: false
      },
      scale: {
        ticks: {
          maxTicksLimit: 1,
          display: false
        }
      }
    };
    // end radar chart
  }
}
